import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useInView } from 'react-intersection-observer';
import styled from 'styled-components';
import Common from '../../components/common/Common'
import ItemLi from './ItemLi';
import { getStudyFollowingList } from '../../components/follow/getFollowingList';
import study from '../../assets/icons/illustration/Study.webp'
import MapTogether from '../../components/map/Map'
import instance from '../../lib/apis/interceptor';

export default function StudyList() {
    const myProfile= useSelector((state)=> state.user.myInfo.image);
    const navigate= useNavigate();
    const [ref, inView]= useInView();
    const [togetherLists, setTogetherLists]= useState([]);
    const [showList, setShowList]= useState([]); 
    const [following, setFollowing]= useState([]);
    const [pages, setPages]= useState(0);
    const placeArr= ['1. ÎöùÏÑ¨Ïó≠ Î™®Í∞ÅÏΩîüñ•Ô∏è', '2. ÌöåÍ≥ÑÏÇ¨Í∞Ä ÏïåÎ†§Ï£ºÎäî ÌöåÍ≥ÑÏßÅÎ¨¥ Ïä§ÌÑ∞Îîîüè¶', '3. Ï†úÏïΩ/ Î∞îÏù¥Ïò§ ÏÇ∞ÏóÖÏóê ÎåÄÌïú Ïä§ÌÑ∞Îîîüíä','4. UI/ UX ÎîîÏûêÏù¥ÎÑàÏôÄ Ìï®ÍªòÌïòÎäî Ïä§ÌÑ∞Îîîüé®','5. Ìï¥Ïô∏ÏòÅÏóÖ ÏßÅÎ¨¥ÌÉêÏÉâ Î™®ÏûÑ‚úàÔ∏è'];
    
    const goTogetherUpload= ()=>{
        navigate('/together/upload');
    }

    useEffect(()=>{
        async function fetchMyStudyList(){
            let list= await getStudyFollowingList();
            setFollowing([...list]);
        }
        fetchMyStudyList();
    },[]);

    useEffect(() => {
        function postSort(a, b) {
        if (a.createdAt < b.createdAt) {
            return 1;
        }
        if (a.createdAt > b.createdAt) {
            return -1;
        }
        return 0;
        }

        togetherList()
            .then((res) => {
                const list= res.flat(1).sort(postSort);
                setTogetherLists(list);
                setShowList(list.slice(pages * 10, pages * 10 + 10));
                setPages(pages + 1);
        })
        .catch((error)=>{
            console.log(error);
        });
    }, [following]);
    
        const togetherList = async () => {
            try {
                const togetherFollowList = await Promise.all(
                    following.map(async (list) => {
                        const res = await instance.get(
                            `/product/${list.accountname}/?limit=0&skip=0`
                        );
                        return res.data?.product;
                    })
                );
                return togetherFollowList;
            } catch (error) {
                console.log(error);
            }
        };

        const addShowStudy= ()=> {
            const addStudyList= togetherLists.slice(pages * 10, pages * 10 + 10);
            setShowList([...showList, ...addStudyList]);
            setPages(pages + 1);
        };
    
        useEffect(()=> {
            if(inView){
                addShowStudy();
            }
        },[inView]);

        const goStudyUpload= ()=>{
            navigate(`/together/upload`);
        }

        const pageTitle = 'STUDY PAGE';
        const pageDesc = `Î©òÌÜ†ÎßÅÏàòÏóÖÏùÑ ÌÜµÌïòÏó¨ ÌïôÏäµÌö®Í≥ºÎ•º ÎÇº Ïàò ÏûàÏúºÎ©∞, ÏõêÌïòÎäî Ïä§ÌÑ∞Îîî Í∑∏Î£πÏùÑ ÌòïÏÑ±Ìï®ÏúºÎ°úÏç® Ìï®Íªò ÏÑ±Ïû•Ìï† Ïàò ÏûàÏäµÎãàÎã§`;
        const page= (
        <StudyDiv>
            <Study>
                <StudyPicture>
                    <img src={study} alt='Ïä§ÌÑ∞Îîî ÌéòÏù¥ÏßÄ ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ'/>
                </StudyPicture>
                <StudyTitle>EDUKET STUDY</StudyTitle>

                <Div>
                    <StudyDescription>ÏóêÎìÄÏºìÏóêÏÑú ÎÇòÏóêÍ≤å ÎßûÎäî Ïä§ÌÑ∞Îîî/Î™®ÏûÑÏùÑ Ï∞æÏïÑÎ≥¥ÏÑ∏Ïöî!</StudyDescription>
                    <MapBox>
                        <MapTogether/>
                        <StudyLists>
                            <p>üî•Ïù∏Í∏∞ÏûàÎäî EDUKET STUDYüî•</p>
                            { !placeArr
                                ? []
                            : placeArr.map((item)=>(
                                <ul>
                                    <li><span>{item}</span></li>
                                </ul>
                            ))}
                        </StudyLists>
                    </MapBox>

                    <WriteSection>
                        <WriteDiv>
                            <img src={myProfile} alt="ÎÇòÏùò ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ" />
                            <BoxDiv onClick={goStudyUpload}><p> ÎßåÎì§Í≥† Ïã∂ÏùÄ Ïä§ÌÑ∞Îîî Î∞è Î™®ÏûÑÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî?</p></BoxDiv>
                        </WriteDiv>
                        <button onClick={goStudyUpload} aria-label="Ïä§ÌÑ∞Îîî ÏûëÏÑ±"> ÏûëÏÑ±ÌïòÍ∏∞ </button>
                    </WriteSection>
                    
                    { togetherLists.length ? (
                            <>
                                <Ul>
                                    {!togetherLists 
                                    ? []
                                    : showList.map((data)=> (
                                        <Li>
                                            <ItemLi data={data}/>
                                        </Li>
                                    ))}
                                    <div ref={ref}></div>
                                </Ul>
                                <button onClick={goTogetherUpload} aria-label="Ïä§ÌÑ∞Îîî ÏûëÏÑ±"></button>
                            </>
                        ) : (
                            <>
                                <Alert> Loading . .</Alert>
                            </>
                        )}
                </Div>
        </Study>
    </StudyDiv>
    )
    return (
        <>
            <Common page={page} title={pageTitle} desc={pageDesc} />
        </>
    );
}
const StudyDiv= styled.div`
    background-color: #f1f2f3;
    padding: 40px 0 50px;
`
const Study = styled.div`
    position: relative;
    max-width: 1400px;
    margin: 0 auto;
`
const Div= styled.div`
    border-radius: 10px;
    margin-top: -10px;
`
const StudyPicture= styled.div`
    & img {
        display: block;
        width: 100%;
        height: 80%;
        margin: 0 auto;
        border-radius: 10px;
        }
`
const StudyTitle= styled.strong`
    position: absolute;
    width: fit-content;

    font-family: "Frutiger-lt-pro-600";
    font-size: 35px;
    font-weight: 600;
    color: #3a3a3a;
    text-shadow: 2px 2px 0px #90979f;
    
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px 20px 20px;
`
const StudyDescription= styled.p`
    font-family: "Noto_Sans_KR-400";
    font-size: 20px;
    line-height: 80px;
    text-align: center;
    color: #777;
    margin: 10px 15px 0 15px;
`
const WriteSection= styled.div`
    display: flex;

    & button {
        width: 280px;
        margin: 0 15px; 

        font-family: "Noto_Sans_KR-400";
        font-size: 20px;
        color: #3a3a3a;

        border: 1px solid rgba(0,0,0,0.09);
        border-radius: 10px;
        box-shadow: 0 1px 4px rgba(0,0,0,0.04);
        background-color: #ffff;
    }
`
const WriteDiv= styled.div`
    display: flex;
    align-items: center;
    gap: 10px;

    width: 96%;
    padding: 10px;
    margin: 0 15px;

    border: 1px solid rgba(0,0,0,0.09);
    border-radius: 10px;
    box-shadow: 0 1px 4px rgba(0,0,0,0.04);
    background-color: #ffff;

    & img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        padding: 12px;
    }
`
const BoxDiv= styled.div`
    width: 90%;
    padding: 15px;

    border: 1px solid rgba(0,0,0,0.09);
    border-radius: 15px;
    box-shadow: 0 1px 4px rgba(0,0,0,0.04);
    background-color: #f1f2f3;

    cursor: pointer;

    & p {
        font-family: "Noto_Sans_KR-400";
        font-size: 20px;
        color: #94A3B8;
    }
`
const MapBox= styled.div`
    display: flex;
    justify-content: space-around;
    align-items: center;

    width: 96%;
    padding: 20px 10px;
    margin: 5px 15px 30px 15px;

    border: 1px solid rgba(0,0,0,0.09);
    border-radius: 10px;
    box-shadow: 0 1px 4px rgba(0,0,0,0.04);
    background-color: #ffff;
`
const StudyLists= styled.div`
    margin-right: 80px;

    & p {
    font-family: "Noto_Sans_KR-600";
    font-size: 24px;
    font-weight: 600;
    color: #3a3a3a;

    margin-bottom: 30px;
    }

    & li {
        font-family: "Noto_Sans_KR-400";
        list-style: none;
        font-size: 17px;
        color: #777;
        line-height: 35px;
        margin-left: 25px;
    }
`
const Ul= styled.ul`
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    padding: 0 0;
    margin: 0 0;
    line-height: 0;

    @media screen and (max-width: 800px) {
        flex-direction: column;
    }   
`
const Li= styled.li`
    flex: 0 0 33.33333%;
    margin: 0 0 30px 0;
    padding: 0 0 0;
    vertical-align: top;

    @media screen and (max-width: 1200px) {
        flex: 0 0 33.5%;
    }

    @media screen and (max-width: 1100px) {
        flex:0 0 50%;
    }
    
    @media screen and (max-width: 800px) {
        width: 100%;
    }
`
const Alert= styled.p`
    font-family: "Noto_Sans_KR-600";
    font-size: 35px;
    text-align: center;
    color: #C63D2F;
    padding: 50px 0 30px;
`